	module.exports = {"data":{"wordpressPost":{"title":"Locked-in syndrome article coverage","content":"<p>A month after launch, some of our first papers are still being picked up in the media (see today&#8217;s <a href=\"http://www.imt.ie/clinical/2011/03/delays-seen-in-recording-ovarian-cancer-diagnosis-in-primary-care.html\" target=\"_self\">Irish Medical Times</a> for coverage of our paper on delays in recording diagnoses of ovarian cancer).</p>\n<p>The paper by <a href=\"http://bmjopen.bmj.com/content/early/2011/02/16/bmjopen-2010-000039.full\" target=\"_blank\">Bruno et al.</a> on self-assessed well-being of locked-in syndrome patients received considerable coverage, including <a href=\"http://news.bbc.co.uk/today/hi/today/newsid_9406000/9406385.stm\" target=\"_blank\">this interview</a> on BBC Radio&#8217;s flagship news broadcast The Today Programme, and coverage in <a href=\"http://www.newscientist.com/article/dn20162-most-lockedin-people-are-happy-survey-finds.html\" target=\"_blank\">New Scientist</a>, <a href=\"http://www.bbc.co.uk/news/health-12551753\" target=\"_blank\">BBC News</a>, and <a href=\"http://www.independent.co.uk/life-style/health-and-families/health-news/why-there-may-be-life-inside-the-diving-bell-2223938.html#\" target=\"_blank\">The Independent</a> among many others.</p>\n<p>The article has also generated comments via e-letters and our Disqus blog-style commenting feature. You can read them, and comment yourself, <a href=\"http://bmjopen.bmj.com/content/early/2011/02/16/bmjopen-2010-000039.full#responses\" target=\"_blank\">here</a>.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby for BMJ blog sites"}}},"pathContext":{"id":"75d341de-770d-5d61-99c9-af6c01b7b837"}}

